.AddProtocol {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.AddProtocol h2,
.ProtocolForm h2 {
  color: #2d5016;
  margin-bottom: 20px;
}

.AddProtocol .error-message,
.ProtocolForm .error-message {
  background: #fce4e4;
  color: #c0392b;
  padding: 10px 15px;
  border-radius: 4px;
  margin-bottom: 20px;
}

.AddProtocol .form-section,
.ProtocolForm .form-section {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.AddProtocol .form-section h3,
.ProtocolForm .form-section h3 {
  color: #2d5016;
  margin: 0 0 15px 0;
  padding-bottom: 10px;
  border-bottom: 2px solid #5d8a3a;
}

.AddProtocol .form-group,
.ProtocolForm .form-group {
  margin-bottom: 15px;
}

.AddProtocol .form-group label,
.ProtocolForm .form-group label {
  display: block;
  font-weight: 600;
  color: #333;
  margin-bottom: 5px;
}

.AddProtocol .form-group input[type="text"],
.AddProtocol .form-group input[type="number"],
.AddProtocol .form-group textarea,
.ProtocolForm .form-group input[type="text"],
.ProtocolForm .form-group input[type="number"],
.ProtocolForm .form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1em;
  box-sizing: border-box;
}

.AddProtocol .form-group input:focus,
.AddProtocol .form-group textarea:focus,
.ProtocolForm .form-group input:focus,
.ProtocolForm .form-group textarea:focus {
  outline: none;
  border-color: #5d8a3a;
}

.AddProtocol .checkbox-group,
.ProtocolForm .checkbox-group {
  display: flex;
  gap: 20px;
}

.AddProtocol .checkbox-group label,
.ProtocolForm .checkbox-group label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: normal;
  cursor: pointer;
}

.AddProtocol .checkbox-group input[type="checkbox"],
.ProtocolForm .checkbox-group input[type="checkbox"] {
  width: 18px;
  height: 18px;
}

.AddProtocol .timeline-day,
.ProtocolForm .timeline-day {
  background: white;
  padding: 15px;
  border-radius: 6px;
  margin-bottom: 15px;
  border-left: 4px solid #5d8a3a;
}

.AddProtocol .day-header,
.ProtocolForm .day-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.AddProtocol .day-inputs,
.ProtocolForm .day-inputs {
  display: flex;
  gap: 15px;
  align-items: center;
}

.AddProtocol .day-inputs label,
.ProtocolForm .day-inputs label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
}

.AddProtocol .day-input,
.ProtocolForm .day-input {
  width: 70px !important;
  text-align: center;
}

.AddProtocol .remove-btn,
.ProtocolForm .remove-btn {
  background: #e74c3c;
  color: white;
  border: none;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
}

.AddProtocol .remove-btn:hover,
.ProtocolForm .remove-btn:hover {
  background: #c0392b;
}

.AddProtocol .treatments-section,
.ProtocolForm .treatments-section {
  margin-top: 10px;
}

.AddProtocol .treatments-section > label,
.ProtocolForm .treatments-section > label {
  display: block;
  font-weight: 600;
  color: #666;
  margin-bottom: 8px;
  font-size: 0.9em;
}

.AddProtocol .treatment-row,
.ProtocolForm .treatment-row {
  display: flex;
  gap: 10px;
  margin-bottom: 8px;
  align-items: center;
}

.AddProtocol .treatment-row input,
.ProtocolForm .treatment-row input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 0.9em;
}

.AddProtocol .treatment-row input:focus,
.ProtocolForm .treatment-row input:focus {
  outline: none;
  border-color: #5d8a3a;
}

.AddProtocol .remove-treatment-btn,
.ProtocolForm .remove-treatment-btn {
  background: #95a5a6;
  color: white;
  border: none;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 14px;
  flex-shrink: 0;
}

.AddProtocol .remove-treatment-btn:hover,
.ProtocolForm .remove-treatment-btn:hover {
  background: #7f8c8d;
}

.AddProtocol .add-treatment-btn,
.ProtocolForm .add-treatment-btn {
  background: none;
  border: 1px dashed #5d8a3a;
  color: #5d8a3a;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85em;
  margin-top: 5px;
}

.AddProtocol .add-treatment-btn:hover,
.ProtocolForm .add-treatment-btn:hover {
  background: #f0f7ec;
}

.AddProtocol .add-day-btn,
.ProtocolForm .add-day-btn {
  background: #5d8a3a;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

.AddProtocol .add-day-btn:hover,
.ProtocolForm .add-day-btn:hover {
  background: #4a6f2e;
}

.AddProtocol .form-actions,
.ProtocolForm .form-actions {
  margin-top: 20px;
  text-align: right;
}

.AddProtocol .form-actions button,
.ProtocolForm .form-actions button {
  background: #2d5016;
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.1em;
  font-weight: 600;
  height:unset;
}

.AddProtocol .form-actions button:hover:not(:disabled),
.ProtocolForm .form-actions button:hover:not(:disabled) {
  background: #1e3a10;
}

.AddProtocol .form-actions button:disabled,
.ProtocolForm .form-actions button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.AddProtocol .form-actions .cancel-btn,
.ProtocolForm .form-actions .cancel-btn {
  background: #95a5a6;
  margin-right: 10px;
}

.AddProtocol .form-actions .cancel-btn:hover,
.ProtocolForm .form-actions .cancel-btn:hover {
  background: #7f8c8d;
}

/* Protocol expanded view */
.protocol-expanded {
  padding: 20px;
  background: #fafafa;
  border-left: 4px solid #5d8a3a;
}

.protocol-expanded.readonly {
  border-left-color: #95a5a6;
}

.protocol-expanded .system-protocol-notice {
  background: #fff3cd;
  color: #856404;
  padding: 10px 15px;
  border-radius: 4px;
  margin-bottom: 15px;
  font-style: italic;
}

/* Protocol details readonly view */
.protocol-details .detail-section {
  margin-bottom: 15px;
}

.protocol-details .detail-section strong {
  color: #2d5016;
}

.protocol-details .timeline-readonly {
  margin-top: 10px;
}

.protocol-details .timeline-day-readonly {
  background: white;
  padding: 12px;
  margin-bottom: 10px;
  border-radius: 4px;
  border-left: 3px solid #5d8a3a;
}

.protocol-details .day-label {
  font-weight: 600;
  color: #2d5016;
  margin-bottom: 5px;
}

.protocol-details .day-event {
  color: #333;
  margin-bottom: 8px;
}

.protocol-details .treatments-list {
  margin: 0;
  padding-left: 20px;
  color: #666;
}

.protocol-details .treatments-list li {
  margin-bottom: 4px;
}

.protocol-details .treatments-list span {
  margin-right: 8px;
}

.protocol-details .treatment-note {
  color: #888;
  font-style: italic;
}

/* Protocol actions */
.protocol-actions {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid #ddd;
}

.edit-protocol-btn {
  background: #5d8a3a;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
}

.edit-protocol-btn:hover {
  background: #4a6f2e;
}
