.Charts {
  padding: 20px;
}

.Charts .chart-selector {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.Charts .chart-selector label {
  font-weight: 600;
  color: #333;
}

.Charts .chart-selector select {
  padding: 12px 12px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: white;
  min-width: 250px;
  height: 44px;
}

.Charts .chart-selector select:focus {
  outline: none;
  border-color: #5d8a3a;
}

.Charts .chart-container {
  display: flex;
  justify-content: center;
}

.Charts .chart-container .Chart {
  width: 100%;
  max-width: 500px;
  min-height: 300px;
}

.Charts .chart-container .Chart h3 {
  text-align: center;
  color: #2d5016;
  margin-bottom: 20px;
}
