.notes-expanded {
  background: #f9f9f9;
  padding: 15px 20px;
  border-left: 4px solid #5d8a3a;
}

.notes-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
}

.notes-header h4 {
  margin: 0;
  color: #2d5016;
  font-size: 1em;
}

.notes-icon {
  font-size: 1.2em;
}

.notes-list {
  margin-bottom: 15px;
}

.no-notes {
  color: #888;
  font-style: italic;
  margin: 0;
}

.note-item {
  background: white;
  padding: 10px 12px;
  margin-bottom: 8px;
  border-radius: 4px;
  border-left: 3px solid #5d8a3a;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

.note-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.85em;
  color: #666;
  margin-bottom: 6px;
}

.note-author {
  font-weight: 600;
  color: #2d5016;
}

.note-date {
  color: #999;
}

.note-text {
  color: #333;
  line-height: 1.4;
  white-space: pre-wrap;
}

.add-note-section {
  display: flex;
  gap: 10px;
  align-items: flex-start;
}

.note-input {
  flex: 1;
  padding: 8px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-family: inherit;
  font-size: 0.95em;
  resize: vertical;
  min-height: 60px;
}

.note-input:focus {
  outline: none;
  border-color: #5d8a3a;
}

.add-note-button {
  padding: 8px 16px;
  background: #5d8a3a;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  white-space: nowrap;
}

.add-note-button:hover:not(:disabled) {
  background: #4a6f2e;
}

.add-note-button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* Note icon in table row */
.note-icon-cell {
  cursor: pointer;
  font-size: 1.1em;
  opacity: 0.6;
  transition: opacity 0.2s;
}

.note-icon-cell:hover {
  opacity: 1;
}

.note-icon-cell.has-notes {
  opacity: 1;
  color: #5d8a3a;
}
