.StatCard {
  width: 300px;
  height: 200px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.StatCard .stat-title {
  color: white;
  font-weight: bold;
  height: 25%;
  border-radius: 10px 10px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  padding: 0 10px;
}

.StatCard .stat-content {
  height: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.StatCard .stat-value {
  font-size: 5em;
  font-weight: bold;
  color: white;
}
